  --primary-color: rgb(17, 86, 102);
  --primary-color-darker: rgb(9, 48, 56);
}

* {
  box-sizing: border-box;
  outline: 0;
}

body {
  margin: 0;
  padding: 0;
  background: var(--primary-color);
  font-family: 'Open sans', sans-serif;
  font-size: 1.3em;
  line-height: 1.5em;
}

.container {
  max-width: 640px;
  margin: 50px auto;
  background: #fff;
  padding: 20px;
  border-radius: 10px;
}

.generated-password{
  color: var(--primary-color);
  font-size: 2em;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  padding: 5px;
}
.password-lengh{
  
}
input[type=number]::-webkit-inner-spin-button { 
  -webkit-appearance: none;
  
}
input[type=number] { 
 -moz-appearance: textfield;
 appearance: textfield;

}
p{
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
  align-items: center;
}

input{
  border-radius: 5px;
  padding: 10px;
  width: 110px;
  text-align: center;
  margin-left: 10px;
}

input[type="checkbox"] {
  width: 30px;
  height: 40px;
  margin-left: 10px;
}

.btn{
  display: flex;
  justify-content: center;
}
.btn>input{
  border: none;
  background-color: var(--primary-color);
  color: white;
}
.btn>input:hover{
  cursor: pointer;
  background-color: var(--primary-color-darker);
